import React, { useState } from "react";
import moment from "moment";
import {
  View,
  Text,
  TextInput,
  Button,
  FlatList,
  StyleSheet
} from "react-native";

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "skyblue",
    alignItems: "center",
    justifyContent: "center"
  },
  input: {
    textAlign: "center",
    borderWidth: 1,
    borderColor: "gray",
    borderRadius: 5,
    padding: 10,
    margin: 5,
    width: 200
  },
  text: {
    textAlign: "center",
    fontWeight: "bold",
    fontSize: 16
  },
  list: {
    borderWidth: 1,
    borderColor: "gray",
    borderRadius: 5,
    padding: 10,
    margin: 5,
    width: 300,
    height: 200,
    flexGrow: 0
  },

  button: {
    borderRadius: 20
  },

  deleteButton: {
    color: "red"
  }
});

const App = () => {
  const [expenses, setExpenses] = useState([]);
  const [expenseName, setExpenseName] = useState("");
  const [expenseAmount, setExpenseAmount] = useState("");

  const addExpense = () => {
    const newExpense = {
      name: expenseName,
      amount: expenseAmount,
      date: new Date()
    };
    setExpenses([...expenses, newExpense]);
    setExpenseName("");
    setExpenseAmount("");
  };

  const deleteExpense = (index) => {
    const newExpenses = [...expenses];
    newExpenses.splice(index, 1);
    setExpenses(newExpenses);
  };

  const filteredExpenses = expenses.filter((expense) =>
    moment(expense.date).isSame(moment(), "week")
  );

  const totalExpenses = filteredExpenses.reduce(
    (total, expense) => total + parseFloat(expense.amount),
    0
  );

  return (
    <View style={styles.container}>
      <Text style={styles.text}>Add new expense</Text>
      <TextInput
        style={styles.input}
        placeholder="Expense name"
        value={expenseName}
        onChangeText={setExpenseName}
        maxLength={1024}
      />
      <TextInput
        style={styles.input}
        placeholder="Expense amount"
        value={expenseAmount}
        onChangeText={setExpenseAmount}
        maxLength={1024}
      />
      <Button title="Add" onPress={addExpense} style={styles.button} />

      <Text style={styles.text}>
        Total expenses within week: {totalExpenses}
      </Text>

      <FlatList
        style={styles.list}
        data={filteredExpenses}
        renderItem={({ item, index }) => (
          <View>
            <Text style={styles.text}>{item.name}</Text>
            <Text style={styles.text}>{item.amount}</Text>
            <Text style={styles.text}>
              {moment(item.date).format("MMM D, YYYY")}
            </Text>
            <Button
              title="Delete"
              onPress={() => deleteExpense(index)}
              style={(styles.button, styles.deleteButton)}
            />
          </View>
        )}
        keyExtractor={(item, index) => index.toString()}
      />
    </View>
  );
};

export default App;
